.flip-card-outer {
  width: 250px;
  height: 250px;

  &.focus-trigger:focus {
    outline: 5px solid greenyellow;
    outline-offset: 5px;
  }

  .flip-card-inner {
    transform-style: preserve-3d;
    transition: 0.5s linear 0.1s;
    position: relative;
    width: inherit;
    height: inherit;

    &.hover-trigger:hover {
      transform: rotateY(180deg);
    }

    &.showBack {
      transform: rotateY(180deg);
    }

    .card {
      backface-visibility: hidden;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      &.front {
        border-radius: 20px;
        border: 1px solid #a456f0;
        transform: rotateY(0);
        background-color: #12071f;
        color: #fff;
        cursor: pointer;
      }

      //   &.back {
      //     border-radius: 20px;
      //     border: 1px solid #a456f0;
      //     transform: rotateY(180deg) scale(2);
      //     z-index: 100;
      //     background-color: #12071f;
      //     color: #fff;
      //     cursor: pointer;
      //   }
    }

    .card-two {
      backface-visibility: hidden;
      position: fixed;
      display: block;
      top: 50%;
      left: 50%;
      z-index: 100;
      width: 250px;
      height: 250px;

      // &.front {
      //   border-radius: 20px;
      //   border: 1px solid #a456f0;
      //   transform: rotateY(0);
      //   background-color: #12071f;
      //   color: #fff;
      //   cursor: pointer;
      // }

      &.back {
        border-radius: 20px;
        border: 1px solid #a456f0;
        transform: rotateY(180deg) scale(2);
        transition: transform 2000ms ease-in-out;
        transform-origin: left top;
        z-index: 100;
        background-color: #12071f;
        color: #fff;
        cursor: pointer;
      }
    }
  }
}
